.button {
    height: 3rem;
    border-radius: 1.5rem;
    border: none;
    outline: none !important;
    -webkit-tap-highlight-color: transparent !important;
    width: 100%;
    color: white;
    font-weight: 700;
    background-color: #1596C0;
    transition: ease-in-out 0.1s;

}

.button.pressing {
    opacity: 0.2;
}